import {
    isDefined
} from './Utilities.js';

class ItemHeaderTag {
    constructor( integrationId, integrationName = null ){
        this.integrationId = integrationId;
        this.integrationName = integrationName;

        return this.createItemTag();
    }

    /**
     * Create item tag with the name of the integration
     *
     * @since 0.5
     * 
     * @param {string}    integrationCode - The integration code
     *
     * @return {object}   HTML with the item tag
     */

    createItemTag(){
        // Get integration by integrationId
        let integration = UncannyAutomator.integrations[ this.integrationId ];

        // Create tag element and set the integraiton name
        let container   = $( '<div/>', { 'class': 'item-title__integration clear' }),
            tag         = $( '<div/>', { 'class': 'item-title__tag' });

        if ( isDefined( integration ) ){
            tag.text( integration.name );
        }
        else {
            tag.text( this.integrationName );
        }

        container.append( tag );

        /**
        * Disable user interaction on Integration tag and icon
        *

        tag.on( 'click', function (){
            if ( this.itemId ){
                restCall( 'delete', {
                    ID:           parseInt( this.itemId ),
                    integration:  'new-integration'
                }, function( response ){
                    itemsObject( 'delete', {
                        id: this.itemId
                    });

                    if ( 'deleted-uo-trigger' === response.action ){
                        new ItemCreatorIntegrations( 'trigger', this.itemId );
                    }
                    if ( 'deleted-uo-action' === response.action ){
                        new ItemCreatorIntegrations( 'action', this.itemId );
                    }
                });
            } else {
                new Triggers();
                new Actions();
            }
        });

        */

        // Return tag
        return container;
    }
}

export default ItemHeaderTag;


